module.exports = /* eslint-disable */ {"name":"Form","description":"","props":{"errors":{"type":{"name":"object"},"required":false,"description":"Bind the error message' object in this prop that will be executed when the validation failed.","defaultValue":{"value":"{}","computed":false}},"onChange":{"type":{"name":"func"},"required":false,"description":"The corresponding function that is binded in this prop will trigger when the value of an element has been changed.","defaultValue":{"value":"() => {}","computed":false}},"onEnterKey":{"type":{"name":"func"},"required":false,"description":"The corresponding function that is binded in this prop will trigger when the enter key was pressed.","defaultValue":{"value":"() => {}","computed":false}}},"examples":[{"name":"01-ExampleForm","description":"","code":"import React from 'react';\nimport moment from 'moment';\n\nimport Form, {\n    CalendarInput, CheckBox, FileInput,\n    RadioGroup, SelectInput, TextArea,\n    TextInput\n} from \"doj-react-adminlte/Form\";\nimport ValueButton from \"doj-react-adminlte/ValueButton\";\n\nexport default class ExampleForm extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            birthdate: null,\n            choice1: false,\n            choice2: false,\n            choice3: false,\n            choice4: false,\n            city: null,\n            coffee: null,\n            comments: \"\",\n            name: \"\",\n            password: \"\",\n            file: null\n        };\n\n        this.x = 0;\n    }\n\n    handleChange = (name, value) => {\n        this.setState({[name]: value});\n    };\n\n    isValidDate = current => {\n        const yesterday = moment().subtract(1, 'day');\n        return current.isAfter(yesterday);\n    };\n\n    render() {\n        const errors = {\n            birthdate: [ \"Invalid date.\" ]\n        };\n\n        const cityOptions = [\n            { label: \"Manila\", value: \"Manila\" },\n            { label: \"Makati\", value: \"Makati\" },\n            { label: \"Las Pias City\", value: \"Las Pias City\" },\n            { label: \"Marikina\", value: \"Marikina\" },\n            { label: \"Muntinlupa\", value: \"Muntinlupa\" },\n            { label: \"Quezon City\", value: \"Quezon City\" },\n        ];\n\n        const coffeeOptions = [\n            { label: \"Kopiko\", value: \"Kopiko\" },\n            { label: \"Nescafe\", value: \"Nescafe\" },\n            { label: \"Others\", value: \"Others\" }\n        ];\n\n        return (\n            <Form onChange={this.handleChange} errors={errors}>\n                <div className=\"row\">\n                    <div className=\"col-xs-6\">\n                        <TextInput name=\"name\" value={this.state.name} label=\"Name\"/>\n                    </div>\n                    <div className=\"col-xs-6\">\n                        <TextInput name=\"password\" value={this.state.password} label=\"Password\" type=\"password\"/>\n                    </div>\n                </div>\n                <div className=\"row\">\n                    <div className=\"col-xs-6\">\n                        <CalendarInput name=\"birthdate\" value={this.state.birthdate}\n                                       label=\"Birthdate\" timeFormat={false} isSelectableDate={this.isValidDate}/>\n                    </div>\n                    <div className=\"col-xs-6\">\n                        <SelectInput label=\"City\" name=\"city\" value={this.state.city}\n                                        options={cityOptions} simpleValue clearable/>\n                    </div>\n                </div>\n                <div className=\"row\">\n                    <div className=\"col-xs-12\">\n                        <TextArea name=\"comments\" value={this.state.comments} label=\"Comments\" showCounter/>\n                    </div>\n                </div>\n                <div className=\"row\">\n                    <div className=\"col-xs-6\">\n                        <RadioGroup label=\"Coffee\" name=\"coffee\" value={this.state.coffee}\n                                    options={coffeeOptions} simpleValue firstDefault/>\n                    </div>\n                    <div className=\"col-xs-6\">\n                        <span className=\"text-bold\">Multiple Choice</span>\n                        <CheckBox label=\"Choice 1\" name=\"choice1\" checked={this.state.choice1}/>\n                        <CheckBox label=\"Choice 2\" name=\"choice2\" checked={this.state.choice2}/>\n                        <CheckBox label=\"Choice 3\" name=\"choice3\" checked={this.state.choice3}/>\n                        <CheckBox label=\"Choice 4\" name=\"choice4\" checked={this.state.choice4}/>\n                    </div>\n                </div>\n                <div className=\"row\">\n                    <div className=\"col-xs-6\">\n                        <FileInput name=\"file\" label=\"File Upload\"/>\n                    </div>\n                </div>\n                <div className=\"row\">\n                    <div className=\"col-xs-12\">\n                        <ValueButton className=\"btn btn-primary\">\n                            <i className=\"fa fa-save margin-r-5\"/>Save\n                        </ValueButton>\n                    </div>\n                </div>\n            </Form>\n        );\n    }\n}"},{"name":"02-ExampleForm","description":"","code":"import React from 'react';\n\nimport Form, {\n    CheckBox, RadioGroup, SelectInput, TextInput\n} from \"doj-react-adminlte/Form\";\n\nimport Box from 'doj-react-adminlte/Box';\n\nexport default class ExampleForm extends React.Component{\n    constructor(props){\n        super(props);\n        this.state = {\n            names: null,\n            name: null,\n            coffee: null,\n            choice1: null,\n            choice2: null,\n            brand: null\n        }\n    }\n    handleChange = (name, value) => {\n        this.setState({[name]: value});\n    };\n    render(){\n        const namesOptions = [\n            { label: \"John\", value: \"John\" },\n            { label: \"Doe\", value: \"Doe\" },\n            { label: \"JL\", value: \"JL\" },\n            { label: \"Steph\", value: \"Steph\" },\n            { label: \"Chelz\", value: \"Chelz\" },\n            { label: \"Jose\", value: \"Jose\" },\n            { label: \"Mit\", value: \"Mit\" },\n            { label: \"Wayne\", value: \"Wayne\" },\n            { label: \"Jessy\", value: \"Jessy\" }\n        ];\n        const shoesOptions = [\n            { label: \"Adidas\", value: \"Adidas\" },\n            { label: \"Nike\", value: \"Nike\" },\n            { label: \"Under Armour\", value: \"Under Armour\" },\n            { label: \"New Balance\", value: \"New Balance\" }\n        ];\n\n        const errors = {\n            comments: ['Empty Field']\n        };\n        return(\n            <div>\n                <Box theme=\"box box-solid box-success\"> collapsible={true}>\n                    <Box.Header title='Form Elements'/>\n                    <Box.Body>\n                        <Form onChange={this.handleChange} errors={errors}>\n                            <div>\n                                <div className='row'>\n                                    <div className='col-xs-6'>\n                                        <h4><strong>Searchable Select Input</strong></h4>\n                                        <SelectInput label=\"List of Names\" name=\"names\" value={this.state.names}\n                                                      simpleValue={false} searchable={true}\n                                                      options={namesOptions} clearable/>\n                                    </div>\n                                    <div className='col-xs-6'>\n                                        <span><h4><strong>Disabled Text Input</strong></h4></span>\n                                        <TextInput name=\"name\" disabled={true} feedbackIconClass='fa fa-user' placeholder='Username' value={this.state.name} label=\"Username\"/>\n                                    </div>\n                                </div>\n                                <div className=\"row\">\n                                    <div className=\"col-xs-6\">\n                                        <h4><strong>Disabled RadioGroup</strong></h4>\n                                        <RadioGroup label=\"Brands\" name=\"brand\" value={this.state.brand}\n                                                    options={shoesOptions} simpleValue disabled={true} firstDefault/>\n                                    </div>\n                                    <div className=\"col-xs-6\">\n                                        <h4><strong>CheckBox</strong></h4>\n                                        <CheckBox label=\"Lakers\" name=\"lakers\" checked={this.state.choice1}/>\n                                        <CheckBox label=\"Miami Heat\" name=\"miami\" checked={this.state.choice2}/>\n                                        <CheckBox disabled={true} label=\"Ginebra\" name=\"ginebra\" checked={this.state.choice3}/>\n                                        <CheckBox disabled={true} label=\"Alaska\" name=\"alaska\" checked={this.state.choice4}/>\n                                    </div>\n                                </div>\n                            </div>\n                        </Form>\n                    </Box.Body>\n                </Box>\n            </div>\n        )\n\n\n    }\n\n}"}],"components":[{"name":"CalendarInput","description":"","props":{"clearable":{"type":{"name":"bool"},"required":false,"description":"Set to false to remove the clear button to disable clearing of\nthe value using the built-in clear button of the component.","defaultValue":{"value":"true","computed":false}},"datePicker":{"type":{"name":"bool"},"required":false,"description":"Set to false to use time picker  only","defaultValue":{"value":"true","computed":false}},"disabled":{"type":{"name":"bool"},"required":false,"description":"If true, users won't be able to interact with the component","defaultValue":{"value":"false","computed":false}},"errors":{"type":{"name":"objectOf","value":{"name":"arrayOf","value":{"name":"string"}}},"required":false,"description":"","defaultValue":{"value":"{}","computed":false}},"gridClass":{"type":{"name":"string"},"required":false,"description":"Specifies a Bootstrap 3 grid class"},"iconClass":{"type":{"name":"string"},"required":false,"description":"Specifies the icon class to be displayed within the component","defaultValue":{"value":"\"fa fa-calendar\"","computed":false}},"isSelectableDate":{"type":{"name":"func"},"required":false,"description":"Specify a callback to determine the dates that can be selected.\nThe function receives (currentDate, selectedDate) and shall return\na true or false whether the date is valid or not.","defaultValue":{"value":"() => true","computed":false}},"label":{"type":{"name":"node"},"required":false,"description":"Specifies the text to use as the label"},"manualInput":{"type":{"name":"bool"},"required":false,"description":"Set to false to disable manual input","defaultValue":{"value":"true","computed":false}},"name":{"type":{"name":"string"},"required":true,"description":"Specifies the name of the component. It is used to distinguish elements when\na single form change handler is used"},"onChange":{"type":{"name":"func"},"required":false,"description":"Callback fired when component value changes. Accepts a function with two parameters,\n namely field and value","defaultValue":{"value":"() => {}","computed":false}},"timePicker":{"type":{"name":"bool"},"required":false,"description":"Set to false to use date picker only","defaultValue":{"value":"true","computed":false}},"value":{"type":{"name":"any"},"required":false,"description":"Specifies the current value of this component.","defaultValue":{"value":"null","computed":false}}},"examples":[{"name":"01-SimpleUsage","description":"","code":"import React from 'react';\nimport {CalendarInput} from \"doj-react-adminlte/Form\";\n\nexport default class SimpleUsage extends React.Component {\n    constructor(props) {\n        super(props);\n\n        this.state = {\n            selectedDate: null\n        };\n    }\n\n    handleDateChange = (name, value) => {\n        this.setState({selectedDate: value});\n    };\n\n    render() {\n        const {selectedDate} = this.state;\n\n        return (\n            <div className=\"row\">\n                <div className=\"col-xs-6\">\n                    <CalendarInput name=\"calendar\"\n                                   label=\"Date\"\n                                   value={selectedDate}\n                                   onChange={this.handleDateChange}/>\n                </div>\n            </div>\n        );\n    }\n}"},{"name":"02-ClearableCalendarInput","description":"","code":"import React from 'react';\nimport Form, {CalendarInput, CheckBox} from \"doj-react-adminlte/Form\";\nimport ValueButton from \"doj-react-adminlte/ValueButton\";\n\nexport default class ClearableCalendarInput extends React.Component {\n    constructor(props) {\n        super(props);\n\n        this.state = {\n            date: null,\n            clearable: true\n        };\n    }\n\n    handleFormChange = (name, value) => {\n        this.setState({[name]: value});\n    };\n\n    handleClearClick = () => {\n        this.setState({date: null});\n    };\n\n    render() {\n        const {date, clearable} = this.state;\n        return (\n            <div>\n                <Form onChange={this.handleFormChange}>\n                    <div className=\"row\">\n                        <div className=\"col-xs-6\">\n                            <CalendarInput name=\"date\"\n                                           label=\"Date\"\n                                           value={date}\n                                           clearable={clearable}\n                                           />\n                        </div>\n                    </div>\n                    <div className=\"row\">\n                        <div className=\"col-xs-6\">\n                            <ValueButton className=\"btn btn-primary btn-block\"\n                                         onClick={this.handleClearClick}>\n                                Clear\n                            </ValueButton>\n                            <CheckBox label=\"Clearable\"\n                                      name=\"clearable\"\n                                      checked={clearable}/>\n                        </div>\n                    </div>\n                </Form>\n            </div>\n        );\n    }\n}"},{"name":"03-DisabledCalendarInput","description":"","code":"import React from 'react';\nimport Form, {CalendarInput, CheckBox} from \"doj-react-adminlte/Form\";\n\nexport default class DisabledCalendarInput extends React.Component {\n    constructor(props) {\n        super(props);\n\n        this.state = {\n            date: null,\n            disabled: true\n        };\n    }\n\n    handleDateChange = (name, value) => {\n        this.setState({[name]: value});\n    };\n\n    render() {\n        const {date,disabled} = this.state;\n\n        return (\n            <Form onChange={this.handleDateChange}>\n                <div className=\"row\">\n                    <div className=\"col-xs-6\">\n                        <CalendarInput name=\"date\"\n                                       label=\"Date\"\n                                       value={date}\n                                       disabled={disabled} />\n                        <CheckBox label=\"Disabled\"\n                                  name=\"disabled\"\n                                  checked={disabled}/>\n                    </div>\n                </div>\n            </Form>\n        );\n    }\n}"},{"name":"04-IconClass","description":"","code":"import React from 'react';\nimport Form, {CalendarInput,RadioGroup} from \"doj-react-adminlte/Form\";\n\nexport default class IconClass extends React.Component {\n    constructor(props) {\n        super(props);\n\n        this.state = {\n            selectedDate: null,\n            icon: \"fa fa-calendar\"\n        };\n    }\n\n    handleDateChange = (name, value) => {\n        this.setState({[name]: value});\n    };\n\n    render() {\n        const {selectedDate,icon} = this.state;\n        const iconOptions = [\n            {label: \"fa fa-calendar\", value: \"fa fa-calendar\"},\n            {label: \"fa fa-calendar-check-o\", value: \"fa fa-calendar-check-o\"},\n            {label: \"fa fa-calendar-minus-o\", value: \"fa fa-calendar-minus-o\"},\n            {label: \"fa fa-calendar-plus-o\", value: \"fa fa-calendar-plus-o\"},\n            {label: \"fa fa-calendar-times-o\", value: \"fa fa-calendar-times-o\"},\n            {label: \"fa fa-calendar-o\", value: \"fa fa-calendar-o\"}\n        ];\n\n        return (\n            <Form onChange={this.handleDateChange}>\n                <div className=\"row\">\n                    <div className=\"col-xs-6\">\n                        <CalendarInput name=\"selectedDate\"\n                                       label=\"Date\"\n                                       value={selectedDate}\n                                       iconClass={icon}/>\n                    </div>\n                    <div className=\"col-xs-3\">\n                        <RadioGroup label=\"Icons\"\n                                    name=\"icon\"\n                                    value={icon}\n                                    options={iconOptions}\n                                    simpleValue firstDefault/>\n                    </div>\n                </div>\n            </Form>\n        );\n    }\n}"},{"name":"05-IsSelectableDate","description":"","code":"import React from 'react';\nimport moment from 'moment';\nimport {CalendarInput} from \"doj-react-adminlte/Form\";\n\n\nexport default class IsSelectableDate extends React.Component {\n    constructor(props) {\n        super(props);\n\n        this.state = {\n            date1: null,\n            date2: null,\n            date3: null,\n            date4: null\n        };\n    }\n\n    isCurrentOrFutureDate = current => {\n        const yesterday = moment().subtract(1, 'day');\n        return current.isAfter(yesterday);\n    };\n\n    isPastDate = current => {\n        return current.isBefore(moment());\n    };\n\n    isNotWednesday = current => {\n        return current.day()!==3;\n    };\n\n    isWeekday = current => {\n        return current.day() !== 0 && current.day() !== 6;\n    };\n\n    handleDateChange = (name, value) => {\n        this.setState({[name]: value});\n    };\n\n    render() {\n        const {date1,date2, date3, date4} = this.state;\n\n        return (\n            <div className=\"row\">\n                <div className=\"col-xs-6\">\n                    <CalendarInput name=\"date1\"\n                                   label=\"Past dates disabled\"\n                                   value={date1}\n                                   onChange={this.handleDateChange}\n                                   isSelectableDate={this.isCurrentOrFutureDate}/>\n                </div>\n                <div className=\"col-xs-6\">\n                    <CalendarInput name=\"date2\"\n                                   label=\"Future dates disabled\"\n                                   value={date2}\n                                   onChange={this.handleDateChange}\n                                   isSelectableDate={this.isPastDate}/>\n                </div>\n                <div className=\"col-xs-6\">\n                    <CalendarInput name=\"date3\"\n                                   label=\"Wednesdays disabled\"\n                                   value={date3}\n                                   onChange={this.handleDateChange}\n                                   isSelectableDate={this.isNotWednesday}/>\n                </div>\n                <div className=\"col-xs-6\">\n                    <CalendarInput name=\"date4\"\n                                   label=\"Weekends disabled\"\n                                   value={date4}\n                                   onChange={this.handleDateChange}\n                                   isSelectableDate={this.isWeekday}/>\n                </div>\n            </div>\n        );\n    }\n}"},{"name":"06-DateAndTimePicker","description":"","code":"import React from 'react';\nimport {CalendarInput} from \"doj-react-adminlte/Form\";\n\nexport default class DateAndTimePicker extends React.Component {\n    constructor(props) {\n        super(props);\n\n        this.state = {\n            date: null\n        };\n    }\n\n    handleDateChange = (name, value) => {\n        this.setState({[name]: value});\n    };\n\n    render() {\n        const {date} = this.state;\n\n        return (\n            <div className=\"row\">\n                <div className=\"col-xs-4\">\n                    <CalendarInput name=\"date\"\n                                   label=\"Both pickers enabled\"\n                                   value={date}\n                                   onChange={this.handleDateChange}/>\n                </div>\n                <div className=\"col-xs-4\">\n                    <CalendarInput name=\"date\"\n                                   label=\"Time picker only \"\n                                   value={date}\n                                   onChange={this.handleDateChange}\n                                   datePicker={false}/>\n                </div>\n                <div className=\"col-xs-4\">\n                    <CalendarInput name=\"date\"\n                                   label=\"Date picker only\"\n                                   value={date}\n                                   onChange={this.handleDateChange}\n                                   timePicker={false}/>\n                </div>\n                <div className=\"col-xs-12\">\n                    <span style={{fontWeight: 'bold'}}>Selected: </span>\n                    { date ? date.format(\"LLLL\") : \"No selected date\" }\n                </div>\n            </div>\n        );\n    }\n}"},{"name":"07-DisableManualInput","description":"","code":"import React from 'react';\nimport {CalendarInput} from \"doj-react-adminlte/Form\";\n\nexport default class DisableManualInput extends React.Component {\n    constructor(props) {\n        super(props);\n\n        this.state = {\n            date: null\n        };\n    }\n\n    handleDateChange = (name, value) => {\n        this.setState({[name]: value});\n    };\n\n    render() {\n        const {date} = this.state;\n\n        return (\n            <div className=\"row\">\n                <div className=\"col-xs-4\">\n                    <CalendarInput name=\"date\"\n                                   label=\"Disabled Manual Input\"\n                                   manualInput={false}\n                                   value={date}\n                                   onChange={this.handleDateChange}/>\n                </div>\n            </div>\n        );\n    }\n}"}]},{"name":"CheckBox","description":"","props":{"checked":{"type":{"name":"bool"},"required":false,"description":"Sets the checked state of the component.","defaultValue":{"value":"false","computed":false}},"disabled":{"type":{"name":"bool"},"required":false,"description":"Enables/disables user interaction with the component.","defaultValue":{"value":"false","computed":false}},"inline":{"type":{"name":"bool"},"required":false,"description":"Set to true if you want the checkboxes to appear on the same line.","defaultValue":{"value":"false","computed":false}},"label":{"type":{"name":"node"},"required":true,"description":"Specifies the text to use as the label"},"name":{"type":{"name":"string"},"required":true,"description":"Specifies the name of the component.\nIt is used to distinguish elements\nwhen a single form change handler is used"},"onChange":{"type":{"name":"func"},"required":false,"description":"Callback fired when component value changes.\nAccepts a function with two parameters, namely field and value","defaultValue":{"value":"() => {}","computed":false}}},"examples":[{"name":"01-SimpleCheckBox","description":"","code":"import React from 'react'\nimport Form, {\n    CheckBox\n} from 'doj-react-adminlte/Form';\n\nexport default class SimpleCheckBox extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            samsungCB: false,\n            xiaomiCB: false,\n            appleCB: false,\n            huaweiCB: false\n        };\n    }\n\n    handleChange = (name, value) => {\n        this.setState({\n            [name]: value\n        });\n    };\n\n    render() {\n        const {samsungCB, xiaomiCB, appleCB, huaweiCB} = this.state;\n        return (\n            <Form onChange={this.handleChange}>\n                <div className=\"row\">\n                    <div className=\"col-xs-12\">\n                        <span className=\"text-bold\">Mobile Phone Brands</span>\n                        <CheckBox label=\"Samsung\"\n                                  name=\"samsungCB\"\n                                  checked={samsungCB}/>\n                        <CheckBox label=\"Xiaomi\"\n                                  name=\"xiaomiCB\"\n                                  checked={xiaomiCB}/>\n                        <CheckBox label=\"Apple\"\n                                  name=\"appleCB\"\n                                  checked={appleCB}/>\n                        <CheckBox label=\"Huawei\"\n                                  name=\"huaweiCB\"\n                                  checked={huaweiCB}/>\n                    </div>\n                </div>\n            </Form>\n        );\n    }\n}"},{"name":"02-CheckedByExternal","description":"","code":"import React from 'react'\nimport Form, {\n    CheckBox\n} from 'doj-react-adminlte/Form';\n\nexport default class CheckedByExternal extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            clickmeCB: false\n        };\n    }\n\n    handleChange = (name, value) => {\n        this.setState({\n            [name]: value\n        });\n    };\n\n    handleToggleClick = () =>{\n        this.setState({clickmeCB: !this.state.clickmeCB});\n    };\n\n\n    render() {\n        const {clickmeCB} = this.state;\n        return (\n            <Form onChange={this.handleChange}>\n                <div className=\"row\">\n                    <div className=\"col-xs-3\">\n                        <CheckBox label=\"Click me\"\n                                  name=\"clickmeCB\"\n                                  checked={clickmeCB}/>\n                        <button className=\"btn btn-primary\"\n                                     onClick={this.handleToggleClick}>\n                            <i className=\"margin-r-5\"/>\n                            Click to toggle state\n                        </button>\n                    </div>\n                </div>\n            </Form>\n        );\n    }\n}"},{"name":"03-DisableCheckbox","description":"","code":"import React from 'react'\nimport Form, {\n    CheckBox\n} from 'doj-react-adminlte/Form';\n\nexport default class DisableCheckbox extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            disableCB: false,\n            disabled: false\n\n        };\n    }\n\n    handleChange = (name, value) => {\n        this.setState({\n            [name]: value\n        });\n    };\n\n    handleToggleClick = () => {\n        this.setState({disabled: !this.state.disabled});\n    };\n\n    render() {\n        const {disableCB, disabled} = this.state;\n        const isDisable = this.state.disabled ? \"Disabled\" : \"Enabled\" ;\n        const btnlbl = this.state.disabled ? \"Enable\" : \"Disable\" ;\n        return (\n            <Form onChange={this.handleChange}>\n                <div className=\"row\">\n                    <div className=\"col-xs-12\">\n                        <CheckBox label={isDisable}\n                                  name=\"disableCB\"\n                                  checked={disableCB}\n                                  disabled={disabled}\n                        />\n                        <button className=\"btn btn-primary\"\n                                onClick={this.handleToggleClick} >\n                            Click to {btnlbl} CheckBox\n                        </button>\n                    </div>\n                </div>\n            </Form>\n        );\n    }\n}"},{"name":"04-InlineCheckBox","description":"","code":"import React from 'react'\nimport Form, {\n    CheckBox\n} from 'doj-react-adminlte/Form';\n\nexport default class InlineCheckBox extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            appleCB: false,\n            orangeCB: false,\n            mangoCB: false,\n            grapesCB: false,\n            pineappleCB: false\n        };\n    }\n\n    handleChange = (name, value) => {\n        this.setState({\n            [name]: value\n        });\n    };\n\n    render() {\n        const {appleCB, orangeCB, mangoCB, grapesCB, pineappleCB} = this.state;\n        return (\n                <Form onChange={this.handleChange}>\n                    <div className=\"row\">\n                        <div className=\"col-xs-6\">\n                            <span className=\"text-bold\">Fruit</span>\n                        </div>\n                    </div>\n                    <div className=\"row\">\n                        <div className=\"col-xs-12\">\n                            <CheckBox label=\"Apple\"\n                                      name=\"appleCB\"\n                                      checked={appleCB}\n                                      inline={true}\n                            />\n                            <CheckBox label=\"Orange\"\n                                      name=\"orangeCB\"\n                                      checked={orangeCB}\n                                      inline={true}\n                            />\n                            <CheckBox label=\"Mango\"\n                                      name=\"mangoCB\"\n                                      checked={mangoCB}\n                                      inline={true}\n                            />\n                            <CheckBox label=\"Grapes\"\n                                      name=\"grapesCB\"\n                                      checked={grapesCB}\n                                      inline={true}\n                            />\n                            <CheckBox label=\"Pineapple\"\n                                      name=\"pineappleCB\"\n                                      checked={pineappleCB}\n                                      inline={true}\n                            />\n                        </div>\n                    </div>\n                </Form>\n        );\n    }\n}"}]},{"name":"FileInput","description":"","props":{"disabled":{"type":{"name":"bool"},"required":false,"description":"If true, interaction with the component is disabled","defaultValue":{"value":"false","computed":false}},"errors":{"type":{"name":"objectOf","value":{"name":"arrayOf","value":{"name":"string"}}},"required":false,"description":"","defaultValue":{"value":"{}","computed":false}},"gridClass":{"type":{"name":"string"},"required":false,"description":"Specifies a Bootstrap 3 grid class"},"label":{"type":{"name":"node"},"required":false,"description":"Specifies the text to use as the label"},"name":{"type":{"name":"string"},"required":true,"description":"Specifies the name of the component. It is used to distinguish elements when\n     a single form change handler is used"},"onChange":{"type":{"name":"func"},"required":false,"description":"Callback fired when component value changes. Accepts a function with two parameters,\n     namely field and value","defaultValue":{"value":"() => {}","computed":false}}},"examples":[{"name":"01-SimpleFileInput","description":"","code":"import React from 'react'\nimport Form, {\n    FileInput\n} from 'doj-react-adminlte/Form';\n\nexport default class SimpleFileInput extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            file: null\n        };\n    }\n\n    handleChange = (field, value) => {\n        this.setState({\n            [field]: value\n        });\n    };\n\n    render() {\n        return (\n            <Form onChange={this.handleChange}>\n                <div className=\"row\">\n                    <div className=\"col-xs-6\">\n                        <FileInput name=\"file\" label=\"File Upload\"/>\n                    </div>\n                </div>\n            </Form>\n        );\n    }\n}"}]},{"name":"RadioGroup","description":"","props":{"disabled":{"type":{"name":"bool"},"required":false,"description":"If true, interaction with the component is disabled","defaultValue":{"value":"false","computed":false}},"firstDefault":{"type":{"name":"bool"},"required":false,"description":"If true, selects the first option by default upon loading of the list of options","defaultValue":{"value":"false","computed":false}},"inline":{"type":{"name":"bool"},"required":false,"description":"Set to true to make the list of options appear on the same line","defaultValue":{"value":"false","computed":false}},"label":{"type":{"name":"node"},"required":false,"description":"Specifies the text to use as the label"},"labelKey":{"type":{"name":"string"},"required":false,"description":"Property name of the items in the options list from which the option label to be displayed will be taken.\nDefaults to \"label\"","defaultValue":{"value":"\"label\"","computed":false}},"name":{"type":{"name":"string"},"required":true,"description":"Specifies the name of the component. It is used to distinguish elements when\n     a single form change handler is used"},"onChange":{"type":{"name":"func"},"required":false,"description":"Callback fired when component value changes. Accepts a function with two parameters,\n     namely field and value","defaultValue":{"value":"() => {}","computed":false}},"options":{"type":{"name":"array"},"required":true,"description":"Array of objects to be used as options. By default, uses the properties \"label\" and \"value\" for the labels and\nvalues of each of the options respectively."},"simpleValue":{"type":{"name":"bool"},"required":false,"description":"If set to true, only the value property of the selected option is passed as value parameter in the\nonChange handler. Otherwise, the option object itself is passed.","defaultValue":{"value":"false","computed":false}},"value":{"type":{"name":"any"},"required":false,"description":"Specifies the currently selected option"},"valueKey":{"type":{"name":"string"},"required":false,"description":"Property name of the items in the options list from which the value of the option will be taken.\nDefaults to \"value\"","defaultValue":{"value":"\"value\"","computed":false}}},"examples":[{"name":"01-SimpleRadioGroup","description":"","code":"import React from 'react';\nimport Form, {RadioGroup} from \"doj-react-adminlte/Form\";\n\nexport default class SimpleRadioGroup extends React.Component {\n    constructor(props) {\n        super(props);\n\n        this.state = {\n            gender: null\n        };\n    }\n\n    handleOnChange = (field, value) => {\n        this.setState({[field]: value});\n    };\n\n    render() {\n        const {gender} = this.state;\n        const genderOptions = [\n            {label: \"Male\", value: \"Male\"},\n            {label: \"Female\", value: \"Female\"},\n        ];\n\n        return (\n            <Form onChange={this.handleOnChange}>\n                <div className=\"row\">\n                    <div className=\"col-xs-6\">\n                        <RadioGroup label=\"Gender\"\n                                    name=\"gender\"\n                                    value={gender}\n                                    options={genderOptions}\n                        />\n                    </div>\n                </div>\n            </Form>\n        );\n    }\n}"},{"name":"02-ExternalComponent","description":"","code":"import React from 'react'\nimport Form, {\n    TextInput, RadioGroup\n} from 'doj-react-adminlte/Form';\n\nexport default class ExternalComponent extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            textInputDisabled: false,\n            text: null\n        };\n    }\n\n    handleChange = (field, value) => {\n        this.setState({\n            [field]: value\n        });\n    };\n\n    render() {\n        const {textInputDisabled, text} = this.state;\n        const switchOptions = [\n            {label: \"On\", value: false},\n            {label: \"Off\", value: true}\n        ];\n\n        return (\n            <Form onChange={this.handleChange}>\n                <div className=\"row\">\n                    <div className=\"col-xs-9\">\n                        <TextInput name=\"text\"\n                                   placeholder=\"Enter a Text\"\n                                   value={text}\n                                   label=\"Disabled\"\n                                   disabled={textInputDisabled}\n                        />\n                    </div>\n                    <div className=\"col-xs-3\">\n                        <RadioGroup label=\"Switch\"\n                                    name=\"textInputDisabled\"\n                                    value={textInputDisabled}\n                                    options={switchOptions}\n                                    simpleValue\n                        />\n                    </div>\n                </div>\n            </Form>\n        );\n    }\n}"},{"name":"03-DisabledProperty","description":"","code":"import React from 'react'\nimport Form, {\n     RadioGroup\n} from 'doj-react-adminlte/Form';\n\n\nexport default class DisabledProperty extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            radioGroupDisabled: false,\n            radioGroupOption: false\n        };\n    }\n\n    handleChange = (field, value) => {\n        this.setState({\n            [field]: value\n        });\n    };\n\n    handleOnClick = () => {\n        this.setState({\n            radioGroupDisabled: !this.state.radioGroupDisabled\n        });\n    };\n\n    render() {\n        const {radioGroupDisabled, radioGroupOption} = this.state;\n        const switchOptions = [\n            {label: \"On\", value: false},\n            {label: \"Off\", value: true}\n        ];\n        const radiogrouplbl = this.state.radioGroupDisabled ? \"enable\" : \"disable\" ;\n\n        return (\n            <Form onChange={this.handleChange}>\n                <div className=\"row\">\n                    <div className=\"col-xs-3\">\n                        <RadioGroup label=\"Switch\"\n                                    name=\"radioGroupOption\"\n                                    value={radioGroupOption}\n                                    options={switchOptions}\n                                    disabled={radioGroupDisabled}\n                                    simpleValue\n                        />\n                        <button className=\"btn btn-primary\"\n                                onClick={this.handleOnClick}>\n                            Click to {radiogrouplbl}\n                        </button>\n                    </div>\n                </div>\n            </Form>\n        );\n    }\n}"},{"name":"04-InlineRadioGroup","description":"","code":"import React from 'react'\nimport Form, {\n     RadioGroup\n} from 'doj-react-adminlte/Form';\n\nexport default class InlineRadioGroup extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            color: null\n        };\n    }\n\n    handleChange = (field, value) => {\n        this.setState({\n            [field]: value\n        });\n    };\n\n    render() {\n        const {color} = this.state;\n        const colorOptions = [\n            {label: \"Yellow\", value: \"yellow\"},\n            {label: \"Blue\", value: \"blue\"},\n            {label: \"Black\", value: \"black\"},\n            {label: \"Green\", value: \"green\"},\n            {label: \"Red\", value: \"red\"},\n            {label: \"Other Color\", value: \"other_color\"}\n        ];\n\n        return (\n            <Form onChange={this.handleChange}>\n                <div className=\"row\">\n                    <div className=\"col-xs-12\">\n                        <RadioGroup label=\"My favorite color\"\n                                    name=\"color\"\n                                    value={color}\n                                    options={colorOptions}\n                                    inline\n                        />\n                    </div>\n                </div>\n            </Form>\n        );\n    }\n}"},{"name":"05-ValueKeyandLabelKey","description":"","code":"import React from 'react'\nimport Form, {\n    RadioGroup\n} from 'doj-react-adminlte/Form';\n\nexport default class InlineRadioGroup extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            drink: null\n        };\n    }\n\n    handleChange = (field, value) => {\n        this.setState({\n            [field]: value\n        });\n    };\n\n    render() {\n        const {drink} = this.state;\n        const drinkOptions = [\n            {name: \"Coffee\", id: 11},\n            {name: \"Milk\", id: 12},\n            {name: \"Tea\", id: 13},\n            {name: \"Coke\", id: 14},\n            {name: \"Wine\", id: 15},\n            {name: \"Water\", id: 16}\n        ];\n        return (\n            <Form onChange={this.handleChange}>\n                <div className=\"row\">\n                    <div className=\"col-xs-10\">\n                        <RadioGroup label=\"My preferred drink\"\n                                    name=\"drink\"\n                                    labelKey=\"name\"\n                                    valueKey=\"id\"\n                                    value={drink}\n                                    options={drinkOptions}\n                                    inline\n                        />\n                    </div>\n                </div>\n            </Form>\n        );\n    }\n}"},{"name":"06-SimpleValueProperty","description":"","code":"import React from 'react'\nimport Form, {\n    RadioGroup\n} from 'doj-react-adminlte/Form';\n\nexport default class SimpleValueProperty extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            food1: null,\n            food2: null\n        };\n    }\n\n    handleChange = (field, value) => {\n        this.setState({\n            [field]: value\n        });\n    };\n\n    render() {\n        const {food1, food2} = this.state;\n        const foodOptions = [\n            {label: \"Spaghetti\", value: \"spaghetti\"},\n            {label: \"Fried Chicken\", value: \"fried_chicken\"},\n            {label: \"Burger Steak\", value: \"burger_steak\"},\n            {label: \"Hamburger\", value: \"hamburger\"}\n        ];\n\n        return (\n            <Form onChange={this.handleChange}>\n                <div className=\"row\">\n                    <div className=\"col-xs-6\">\n                        <RadioGroup label=\"My favorite food\"\n                                    name=\"food1\"\n                                    value={food1}\n                                    options={foodOptions}\n                                    simpleValue\n                        />\n                    </div>\n                    <div className=\"col-xs-6\">\n                        <RadioGroup label=\"My favorite food\"\n                                    name=\"food2\"\n                                    value={food2}\n                                    options={foodOptions}\n                        />\n                    </div>\n                </div>\n                <div className=\"row\">\n                    <div className=\"col-xs-6\">\n                        <strong>Value:</strong>\n                        <pre>\n                            {food1 || \"null\"}\n                        </pre>\n                    </div>\n                    <div className=\"col-xs-6\">\n                        <strong>Value:</strong>\n                        <pre>\n                           {JSON.stringify(food2, null, 2)}\n                        </pre>\n                    </div>\n                </div>\n            </Form>\n        );\n    }\n}"},{"name":"07-FirstDefaultProperty","description":"","code":"import React from 'react'\nimport Form, {\n    RadioGroup\n} from 'doj-react-adminlte/Form';\n\nexport default class FirstDefaultProperty extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            bank: null\n        };\n    }\n\n    handleChange = (field, value) => {\n        this.setState({\n            [field]: value\n        });\n    };\n\n    render() {\n        const {bank} = this.state;\n        const bankOptions = [\n            {label: \"BDO\", value: \"bdo\"},\n            {label: \"Security Bank\", value: \"security_bank\"},\n            {label: \"BPI\", value: \"bpi\"},\n            {label: \"Landbank\", value: \"landbank\"}\n        ];\n\n        return (\n            <Form onChange={this.handleChange}>\n                <div className=\"row\">\n                    <div className=\"col-xs-6\">\n                        <RadioGroup label=\"My preferred bank\"\n                                    name=\"bank\"\n                                    value={bank}\n                                    options={bankOptions}\n                                    firstDefault\n                        />\n                    </div>\n                </div>\n            </Form>\n        );\n    }\n}"}]},{"name":"SelectInput","description":"","props":{"clearable":{"type":{"name":"bool"},"required":false,"description":"","defaultValue":{"value":"false","computed":false}},"disabled":{"type":{"name":"bool"},"required":false,"description":"","defaultValue":{"value":"false","computed":false}},"errors":{"type":{"name":"objectOf","value":{"name":"arrayOf","value":{"name":"string"}}},"required":false,"description":"","defaultValue":{"value":"{}","computed":false}},"gridClass":{"type":{"name":"string"},"required":false,"description":""},"label":{"type":{"name":"node"},"required":false,"description":""},"name":{"type":{"name":"string"},"required":true,"description":""},"onChange":{"type":{"name":"func"},"required":false,"description":"","defaultValue":{"value":"() => {}","computed":false}},"getOptionLabel":{"type":{"name":"func"},"required":false,"description":""},"getOptionValue":{"type":{"name":"func"},"required":false,"description":""},"options":{"type":{"name":"array"},"required":false,"description":"","defaultValue":{"value":"[]","computed":false}},"placeholder":{"type":{"name":"string"},"required":false,"description":"","defaultValue":{"value":"\"Select...\"","computed":false}},"searchable":{"type":{"name":"bool"},"required":false,"description":"","defaultValue":{"value":"false","computed":false}},"simpleValue":{"type":{"name":"bool"},"required":false,"description":"","defaultValue":{"value":"false","computed":false}},"value":{"type":{"name":"any"},"required":false,"description":"","defaultValue":{"value":"null","computed":false}}},"examples":[]},{"name":"TextArea","description":"","props":{"disabled":{"type":{"name":"bool"},"required":false,"description":"","defaultValue":{"value":"false","computed":false}},"errors":{"type":{"name":"objectOf","value":{"name":"arrayOf","value":{"name":"string"}}},"required":false,"description":"","defaultValue":{"value":"{}","computed":false}},"showCounter":{"type":{"name":"bool"},"required":false,"description":"","defaultValue":{"value":"false","computed":false}},"gridClass":{"type":{"name":"string"},"required":false,"description":""},"label":{"type":{"name":"node"},"required":false,"description":""},"maxLength":{"type":{"name":"number"},"required":false,"description":"","defaultValue":{"value":"500","computed":false}},"name":{"type":{"name":"string"},"required":true,"description":""},"onChange":{"type":{"name":"func"},"required":false,"description":"","defaultValue":{"value":"() => {}","computed":false}},"placeholder":{"type":{"name":"string"},"required":false,"description":""},"readOnly":{"type":{"name":"bool"},"required":false,"description":"","defaultValue":{"value":"false","computed":false}},"value":{"type":{"name":"string"},"required":false,"description":""}},"examples":[]},{"name":"TextInput","description":"","props":{"disabled":{"type":{"name":"bool"},"required":false,"description":"If true, user won't be able to interact with the component.","defaultValue":{"value":"false","computed":false}},"errors":{"type":{"name":"objectOf","value":{"name":"arrayOf","value":{"name":"string"}}},"required":false,"description":"","defaultValue":{"value":"{}","computed":false}},"feedbackIconClass":{"type":{"name":"string"},"required":false,"description":"Specifies the icon class to be displayed within the component"},"feedbackIconLeft":{"type":{"name":"bool"},"required":false,"description":"When set to true, the icon is placed at the left side of the component","defaultValue":{"value":"false","computed":false}},"gridClass":{"type":{"name":"string"},"required":false,"description":"Layout that arranges views of the component","defaultValue":{"value":"\"\"","computed":false}},"label":{"type":{"name":"node"},"required":false,"description":"Specifies the text to use as the label"},"name":{"type":{"name":"string"},"required":true,"description":"Specifies the name of the component. It is used to distinguish elements\nwhen a single form change handler is used."},"onChange":{"type":{"name":"func"},"required":false,"description":"Callback fired when component value changes. Accepts a function with two parameters, namely field and value","defaultValue":{"value":"() => {}","computed":false}},"placeholder":{"type":{"name":"string"},"required":false,"description":"Specifies a short hint that describes the expected value of\nan input field"},"maxLength":{"type":{"name":"number"},"required":false,"description":"Specifies the maximum number of characters allowed in the\ncomponent"},"type":{"type":{"name":"enum","value":[{"value":"'text'","computed":false},{"value":"'password'","computed":false}]},"required":false,"description":"Specifies the type. Can be one of: text, password","defaultValue":{"value":"\"text\"","computed":false}},"value":{"type":{"name":"string"},"required":false,"description":"Specifies the current value for an input field"}},"examples":[{"name":"01-SimpleTextInput","description":"","code":"import React from 'react'\nimport Form, {\n    TextInput\n} from 'doj-react-adminlte/Form';\n\nexport default class SimpleTextInput extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            text:\"\",\n        };\n    }\n\n    handleChange = (field, value) => {\n        this.setState({\n            [field]: value\n        });\n    };\n\n    render() {\n        const {text} = this.state;\n        const errors = {\n            comments: ['Empty Field']\n        };\n        return (\n            <div>\n                <Form onChange={this.handleChange} errors={errors}>\n                    <div className=\"row\">\n                        <div className=\"col-xs-12\">\n                            <TextInput name=\"text\"\n                                       type=\"text\"\n                                       label=\"Text\"\n                                       value={text}\n                                       placeholder=\"Type = Text\"/>\n                        </div>\n                    </div>\n                </Form>\n            </div>\n        );\n    }\n}"},{"name":"02-PasswordInput","description":"","code":"import React from 'react'\nimport Form, {\n    TextInput\n} from 'doj-react-adminlte/Form';\n\nexport default class PasswordInput extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            password:\"\",\n        };\n    }\n\n    handleChange = (field, value) => {\n        this.setState({\n            [field]: value});\n    };\n\n    render() {\n        const {password} = this.state\n        return (\n            <div>\n                <Form onChange={this.handleChange}>\n                    <div className=\"row\" >\n                        <div className=\"col-xs-12\">\n                            <TextInput name=\"password\"\n                                       label=\"Password\"\n                                       value={password}\n                                       type=\"password\"\n                                       placeholder=\"Type = Password\"/>\n                        </div>\n                    </div>\n                </Form>\n            </div>\n        );\n    }\n}"},{"name":"03-FeedbackIcon","description":"","code":"import React from 'react'\nimport Form, {\n    TextInput\n} from 'doj-react-adminlte/Form';\n\nexport default class FeedbackIcon extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            name:\"\",\n            password:\"\"\n        };\n    }\n\n    handleChange = (name, value) => {\n        this.setState({\n            [name]: value});\n    };\n\n    render() {\n        const {name,password} = this.state\n        return (\n            <div>\n                <Form onChange={this.handleChange}>\n                    <div className=\"row\">\n                        <div className=\"col-xs-6\">\n                            <TextInput name=\"name\" label=\"Username\" value={name}\n                                       placeholder=\"Username\"\n                                       feedbackIconClass=\"fa fa-user login-feedback\"\n                                       feedbackIconLeft/>\n                        </div>\n                        <div className=\"col-xs-6\">\n                            <TextInput name=\"password\"\n                                       value={password}\n                                       placeholder=\"Password\"\n                                       feedbackIconClass=\"fa fa-lock login-feedback\"\n                                       label=\"Password\"\n                                       type=\"password\"\n                                       feedbackIconLeft/>\n                        </div>\n                    </div>\n                </Form>\n            </div>\n        );\n    }\n}"},{"name":"04-MaxLengthInput","description":"","code":"import React from 'react'\nimport Form, {\n    TextInput\n} from 'doj-react-adminlte/Form';\n\nexport default class FeedbackIcon extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            name:\"\",\n            password:\"\"\n        };\n    }\n\n    handleChange = (name, value) => {\n        this.setState({\n            [name]: value});\n    };\n\n    render() {\n        const {name,password} = this.state\n        return (\n            <div>\n                <Form onChange={this.handleChange}>\n                    <div className=\"row\">\n                        <div className=\"col-xs-6\">\n                            <TextInput name=\"name\" label=\"Username\" value={name}\n                                       placeholder=\"Username - Max Length 10\"\n                                       maxLength={10}/>\n                        </div>\n                        <div className=\"col-xs-6\">\n                            <TextInput name=\"password\"\n                                       value={password}\n                                       placeholder=\"Password - Max Length 7\"\n                                       label=\"Password\"\n                                       type=\"password\"\n                                       maxLength={7}/>\n                        </div>\n                    </div>\n                </Form>\n            </div>\n        );\n    }\n}"},{"name":"05-DisableInput","description":"","code":"import React from 'react'\nimport Form, {\n    TextInput\n} from 'doj-react-adminlte/Form';\n\nexport default class DisableInput extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            text:\"\"\n        };\n    }\n\n    handleChange = (field, value) => {\n        this.setState({\n            [field]: value});\n    };\n\n    render() {\n        const {text} = this.state;\n        const disabled = text === \"Disable\" ;\n        return (\n            <div>\n                <Form onChange={this.handleChange}>\n                    <div className=\"row\" >\n                        <div className=\"col-xs-12\">\n                            <TextInput name=\"text\"\n                                       label=\"Type Disable\"\n                                       value={text}\n                                       placeholder=\"Type Disable\"\n                                       disabled={disabled}/>\n                        </div>\n                    </div>\n                </Form>\n            </div>\n        );\n    }\n}"},{"name":"06-ReadOnlyInput","description":"","code":"import React from 'react'\nimport Form, {\n    TextInput, RadioGroup\n} from 'doj-react-adminlte/Form';\n\nexport default class ReadOnlyInput extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            city: \"None\"\n        };\n    }\n\n    handleChange = (nameUnused, value) => {\n        this.setState({\n            [nameUnused]: value\n        });\n    };\n\n    render() {\n        const {city} = this.state;\n        const cityOptions = [\n            {label: \"Marikina\", value: \"Marikina\"},\n            {label: \"Quezon City\", value: \"Quezon City\"},\n            {label: \"Makati\", value: \"Makati\"}\n        ];\n        return (\n            <div>\n                <Form onChange={this.handleChange}>\n                    <div className=\"row\">\n                        <div className=\"col-xs-9\">\n                            <TextInput name=\"text\"\n                                       type=\"text\"\n                                       label=\"Text\"\n                                       value={city}\n                                       placeholder=\"Type = Text\"\n                                       disabled={true}/>\n                        </div>\n                        <div className=\"col-xs-3\">\n                            <RadioGroup label=\"City\"\n                                        name=\"city\"\n                                        value={this.state.city}\n                                        options={cityOptions}\n                                        simpleValue firstDefault/>\n                        </div>\n                    </div>\n                </Form>\n            </div>\n        );\n    }\n}"}]}]}