module.exports = /* eslint-disable */ [{"name":"Alert","description":"","props":{"iconClass":{"type":{"name":"string"},"required":false,"description":"Alert Icons. Check AdminLTE for the list of icons available.","defaultValue":{"value":"'fa fa-info'","computed":false}},"show":{"type":{"name":"bool"},"required":false,"description":"If true, displays the component.","defaultValue":{"value":"true","computed":false}},"title":{"type":{"name":"string"},"required":false,"description":"The title header of your Alert component.","defaultValue":{"value":"'Alert!'","computed":false}},"type":{"type":{"name":"string"},"required":false,"description":"Distinguishes an alert. It can be a danger, warning, info and success alert.","defaultValue":{"value":"'alert-info'","computed":false}},"onClose":{"type":{"name":"func"},"required":false,"description":"The function that is binded in this prop will execute after you click the close button.","defaultValue":{"value":"() => {}","computed":false}}},"examples":[{"name":"01-ExampleAlert","description":"","code":"import React from 'react';\nimport Alert from 'doj-react-adminlte/Alert';\n\nexport default class ExampleAlert extends React.Component {\n    render() {\n        return (\n            <React.Fragment>\n                <Alert type='alert-info' title=\"Info Alert!\"\n                       iconClass='fa fa-info'>\n                    <p>This alert box indicates a neutral informative change or action.</p>\n                </Alert>\n                <Alert type='alert-danger' title=\"Danger Alert!\"\n                       iconClass='fa fa-ban'>\n                    <p>This alert box indicates a dangerous action.</p>\n                </Alert>\n                <Alert type='alert-warning' title=\"Warning Alert!\"\n                       iconClass='fa fa-warning'>\n                    <p>This alert box indicates a warning that needs attention.</p>\n                </Alert>\n                <Alert type='alert-success' title=\"Success Alert!\"\n                       iconClass='fa fa-check'>\n                    <p>This alert box indicates a successful action.</p>\n                </Alert>\n            </React.Fragment>\n        );\n    }\n}"},{"name":"02-ExampleAlert","description":"","code":"import React from 'react';\nimport Alert from 'doj-react-adminlte/Alert';\n\nexport default class ExampleAlert extends React.Component{\n    render () {\n        return (\n            <Alert type='alert-info' title=\"Info!\"\n                   iconClass='fa fa-info'>\n                <p>This alert box indicates a neutral informative action.</p>\n            </Alert>\n        );\n    }\n}"},{"name":"03-ExampleAlert","description":"","code":"import React from 'react';\nimport Alert from 'doj-react-adminlte/Alert';\n\nexport default class ExampleAlert extends React.Component {\n    constructor (props) {\n        super(props);\n        this.state ={\n            show: true\n        }\n    }\n\n    handleDismiss = () => {\n        this.setState({show: false});\n    };\n\n    render() {\n        const {show} = this.state;\n        return (\n            <Alert type='alert-warning' title=\"Warning!\" iconClass='fa fa-warning' onClose={this.handleDismiss} show={show}>\n                <p>This is a <b>Warning Alert</b> example.</p>\n            </Alert>\n        );\n    }\n}"}]},{"name":"Avatar","description":"","props":{"firstName":{"type":{"name":"string"},"required":false,"description":"Set the firstname of the Avatar in this prop."},"lastName":{"type":{"name":"string"},"required":false,"description":"Set the lastname of the Avatar in this prop."},"imgSrc":{"type":{"name":"string"},"required":false,"description":"It specifies the URL of the image."},"size":{"type":{"name":"number"},"required":false,"description":"Set the size of the specified Avatar.","defaultValue":{"value":"100","computed":false}},"textSizeRatio":{"type":{"name":"number"},"required":false,"description":"Set the text size in this prop.","defaultValue":{"value":"1.45","computed":false}},"round":{"type":{"name":"bool"},"required":false,"description":"Set to true to change the shape of the component into a rounded shape.","defaultValue":{"value":"false","computed":false}}},"examples":[{"name":"01-ExampleAvatar","description":"","code":"import React from 'react';\nimport Avatar from 'doj-react-adminlte/Avatar';\n\nexport default class ExampleAlert extends React.Component {\n    render() {\n        return (\n            <React.Fragment>\n                <div className=\"row\">\n                    <div className=\"col-md-2\">\n                        <Avatar\n                            firstName='Chelzy'\n                            lastName='Baldovino'\n                            round={true}/>\n                        <h5>Rounded Avatar</h5>\n                    </div>\n                    <div className='col-md-2'>\n                        <Avatar\n                            firstName='Chelzy'\n                            lastName='Baldovino'/>\n                        <h5>Not Rounded Avatar</h5>\n                    </div>\n                </div>\n            </React.Fragment>\n        );\n    }\n}"},{"name":"02-ExampleAvatar","description":"","code":"import React from 'react';\nimport Avatar from \"doj-react-adminlte/Avatar\";\n\nexport default class ExampleAvatar extends React.Component {\n    render() {\n        return (\n            <React.Fragment>\n                <Avatar\n                    firstName='Chelzy'\n                    lastName='Baldovino'\n                    round={true}\n                    size={50}>\n                </Avatar>\n                <Avatar\n                    firstName='Chelzy'\n                    lastName='Baldovino'\n                    round={true}\n                    size={100}>\n                </Avatar>\n                <Avatar\n                    firstName='Chelzy'\n                    lastName='Baldovino'\n                    round={true}\n                    size={150}>\n                </Avatar>\n            </React.Fragment>\n        );\n    }\n}"},{"name":"03-ExampleAvatar","description":"","code":"import React from 'react';\nimport Avatar from \"doj-react-adminlte/Avatar\";\n\nexport default class ExampleAvatar extends React.Component {\n    render() {\n        return (\n            <React.Fragment>\n                <div className=\"row\">\n                    <div className=\"col-xs-3\">\n                        <Avatar\n                            firstName='Chelzy'\n                            lastName='Baldovino'\n                            round={true} />\n                        <p>First and Last Name Initials</p>\n                    </div>\n                    <div className=\"col-xs-3\">\n                        <Avatar\n                            firstName='Chelzy'\n                            round={true} />\n                        <p>First Name Initial</p>\n                    </div>\n                </div>\n            </React.Fragment>\n        );\n    }\n}"}]},{"name":"Box","description":"AdminLTE Box component.","props":{"isLoading":{"type":{"name":"bool"},"required":false,"description":"Returns a boolean value for a postback or loading a content.","defaultValue":{"value":"false","computed":false}},"theme":{"type":{"name":"string"},"required":false,"description":"This prop is set to style a box. See AdminLTE guide for a list of themes.","defaultValue":{"value":"\"box-default\"","computed":false}},"collapsible":{"type":{"name":"bool"},"required":false,"description":"Set to true if box should be collapsible.","defaultValue":{"value":"false","computed":false}}},"examples":[{"name":"01-ExampleBox","description":"","code":"import React from 'react';\nimport Box from \"doj-react-adminlte/Box\";\n\nexport default class ExampleBox extends React.Component {\n    render() {\n        return (\n            <Box>\n                <Box.Header title=\"Test Title\"/>\n                <Box.Body>\n                    Test Body\n                </Box.Body>\n                <Box.Footer>\n                    Test Footer\n                </Box.Footer>\n            </Box>\n        );\n    }\n}"},{"name":"02-ExampleBox","description":"","code":"import React from 'react';\n\nimport Box from \"doj-react-adminlte/Box\";\nimport Form, {TextInput} from 'doj-react-adminlte/Form';\nimport ValueButton from \"doj-react-adminlte/ValueButton\";\n\nexport default class ExampleBox extends React.Component {\n    constructor(props){\n        super(props);\n        this.state = {\n            firstName: \"\",\n            middleName: \"\",\n            lastName: \"\"\n        }\n    }\n    handleFormChange = (name, value) => {\n        this.setState({[name]: value});\n    };\n\n    render() {\n        return (\n            <div>\n                <Box collapsible={true} theme=\"box box-solid box-info\">\n                    <Box.Header title=\"Form\"/>\n                    <Box.Body>\n                        <Form onChange={this.handleFormChange}>\n                            <TextInput name=\"firstName\" label=\"First Name\" value={this.state.firstName}/>\n                            <TextInput name=\"middleName\"  label=\"Middle Name\" value={this.state.middleName}/>\n                            <TextInput name=\"lastName\" label=\"Last Name\" value={this.state.lastName}/>\n                        </Form>\n                    </Box.Body>\n                    <Box.Footer>\n                        <ValueButton className=\"btn btn-primary pull-right\">\n                            Submit\n                        </ValueButton>\n                    </Box.Footer>\n                </Box>\n            </div>\n        );\n    }\n}"},{"name":"03-ExampleBox","description":"","code":"import React from 'react';\nimport Box from \"doj-react-adminlte/Box\";\n\nclass SimpleBox extends React.Component {\n    render() {\n        const {theme, title, message} = this.props;\n\n        return (\n            <div className=\"col-xs-4\">\n                <Box collapsible={true} theme={theme}>\n                    <Box.Header title={title}/>\n                    <Box.Body>\n                        {message}\n                    </Box.Body>\n                </Box>\n            </div>\n        );\n    }\n}\n\nexport default class ExampleBox extends React.Component {\n    render() {\n        return (\n            <React.Fragment>\n                <div className='row'>\n                    <SimpleBox theme=\"box-default\" title=\"Default\"\n                               message=\"Default\"/>\n                    <SimpleBox theme=\"box-primary\" title=\"Primary\"\n                               message=\"Primary\"/>\n                    <SimpleBox theme=\"box-info\" title=\"Info\"\n                               message=\"Info\"/>\n                </div>\n                <div className='row'>\n                    <SimpleBox theme=\"box-warning\" title=\"Warning\"\n                               message=\"Warning\"/>\n                    <SimpleBox theme=\"box-success\" title=\"Success\"\n                               message=\"Success\"/>\n                    <SimpleBox theme=\"box-danger\" title=\"Danger\"\n                               message=\"Danger\"/>\n                </div>\n                <div className='row'>\n                    <SimpleBox theme=\"box-solid box-default\" title=\"Default\"\n                               message=\"Solid Default\"/>\n                    <SimpleBox theme=\"box-solid box-primary\" title=\"Primary\"\n                               message=\"Solid Primary\"/>\n                    <SimpleBox theme=\"box-solid box-info\" title=\"Info\"\n                               message=\"Solid Info\"/>\n                </div>\n                <div className='row'>\n                    <SimpleBox theme=\"box-solid box-warning\" title=\"Warning\"\n                               message=\"Solid Warning\"/>\n                    <SimpleBox theme=\"box-solid box-success\" title=\"Success\"\n                               message=\"Solid Success\"/>\n                    <SimpleBox theme=\"box-solid box-danger\" title=\"Danger\"\n                               message=\"Solid Danger\"/>\n                </div>\n            </React.Fragment>\n        );\n    }\n}"}]},{"name":"DataTable","description":"","props":{"bodyRenderer":{"type":{"name":"func"},"required":false,"description":"It sets the data inside the Table Body."},"className":{"type":{"name":"string"},"required":false,"description":"className gets and sets the value of the class attribute of the specified element. You can also add a CSS class in this prop to style a particular element.","defaultValue":{"value":"\"\"","computed":false}},"columnDefs":{"type":{"name":"arrayOf","value":{"name":"shape","value":{"header":{"name":"string","required":false},"renderValue":{"name":"union","value":[{"name":"string"},{"name":"func"}],"required":false}}}},"required":false,"description":"It sets a column definition properties.","defaultValue":{"value":"[]","computed":false}},"data":{"type":{"name":"arrayOf","value":{"name":"object"}},"required":false,"description":"You can set the data of the table by passing an array or object in this prop.","defaultValue":{"value":"[]","computed":false}},"emptyMessage":{"type":{"name":"string"},"required":false,"description":"The message that will be shown when there's no data in the table.","defaultValue":{"value":"\"No records found.\"","computed":false}},"extra":{"type":{"name":"object"},"required":false,"description":"You can add an extra user-defined props by binding it in this prop.","defaultValue":{"value":"{}","computed":false}},"footRenderer":{"type":{"name":"func"},"required":false,"description":"It sets the data inside the Footer Body."},"identifierKey":{"type":{"name":"string"},"required":false,"description":"It sets the identifier key of the Table.","defaultValue":{"value":"'id'","computed":false}},"onRowClick":{"type":{"name":"func"},"required":false,"description":"Function to invoke when a row is clicked.","defaultValue":{"value":"object => {}","computed":false}},"showEmptyMessageRow":{"type":{"name":"bool"},"required":false,"description":"Set to true so that the empty message will be shown.","defaultValue":{"value":"true","computed":false}},"tableClassName":{"type":{"name":"string"},"required":false,"description":"The className of the table.","defaultValue":{"value":"\"\"","computed":false}}},"examples":[{"name":"01-DataTable","description":"","code":"import React from 'react';\n\nimport DataTable from 'doj-react-adminlte/DataTable';\nimport Box from 'doj-react-adminlte/Box';\n\nconst columnDefs = [\n    {\n        header: 'ID',\n        renderValue: 'id'\n    },\n    {\n        header: 'First Name',\n        renderValue: 'fname'\n    },\n    {\n        header:'Last Name',\n        renderValue: 'lname'\n    }\n];\n\nconst data = [\n    {\n        id: '01',\n        fname: 'Russel',\n        lname: 'Balboa'\n    },\n    {\n        id: '02',\n        fname: 'JL',\n        lname: 'Cotto'\n    },\n    {\n        id: '03',\n        fname: 'John',\n        lname: 'Pacquiao'\n    },\n\n    {\n        id: '04',\n        fname: 'Pia',\n        lname: 'Ayala'\n    },\n    {\n        id: '05',\n        fname: 'Jose',\n        lname: 'Manalo'\n    },\n];\n\nexport default class DataTables extends React.Component{\n    render(){\n\n        return (\n        <div>\n            <Box>\n                <Box.Header title=\"Sample Data\"/>\n                <Box.Body>\n                    <DataTable columnDefs={columnDefs} data={data}/>\n                </Box.Body>\n            </Box>\n        </div>\n        );\n    }\n}"}]},{"name":"DropdownButton","description":"","props":{"className":{"type":{"name":"string"},"required":false,"description":"className gets and sets the value of the class attribute of the specified element. You can also add a CSS class in this prop to style a particular element."},"disabledIfEmpty":{"type":{"name":"bool"},"required":false,"description":"Set to true to disable the button if empty.","defaultValue":{"value":"true","computed":false}},"label":{"type":{"name":"string"},"required":false,"description":"Label of the button."},"menuAlignment":{"type":{"name":"enum","value":[{"value":"'left'","computed":false},{"value":"'right'","computed":false}]},"required":false,"description":"You can set the alignment of the DropdownButton in this prop.","defaultValue":{"value":"'left'","computed":false}},"renderButtonLabel":{"type":{"name":"func"},"required":false,"description":"You can bind a complex function that will manipulate the label of the button."}},"examples":[{"name":"01-ExampleDropdownButton","description":"","code":"import React from 'react';\nimport DropdownButton from \"doj-react-adminlte/DropdownButton\";\n\nexport default class ExampleDropdownButton extends React.Component {\n    handleGenerateReport = () => {\n        console.log(\"Generating report...\")\n    };\n\n    handleLogout = () => {\n        console.log(\"Logging out...\");\n    };\n\n    render() {\n        return (\n            <React.Fragment>\n                <div className=\"row\">\n                    <div className=\"col-xs-2\">\n                        <DropdownButton label=\"Actions\" className=\"btn btn-warning btn-md\">\n                            <DropdownButton.MenuItem label=\"Generate Reports\"\n                                                     onClick={this.handleGenerateReport}/>\n                            <DropdownButton.Divider/>\n                            <DropdownButton.MenuItem label=\"Logout\"\n                                                     onClick={this.handleLogout}/>\n                        </DropdownButton>\n                        <p>Dropdown with Divider</p>\n                    </div>\n                    <div className=\"col-xs-2\">\n                        <DropdownButton label=\"Actions\" className=\"btn btn-info btn-md\">\n                            <DropdownButton.MenuItem label=\"Generate Reports\"\n                                                     onClick={this.handleGenerateReport}/>\n                            <DropdownButton.MenuItem label=\"Logout\"\n                                                     onClick={this.handleLogout}/>\n                        </DropdownButton>\n                        <p>Dropdown without Divider</p>\n                    </div>\n                </div>\n            </React.Fragment>\n        );\n    }\n}"},{"name":"02-ExampleDropdownButton","description":"","code":"import React from 'react';\nimport DropdownButton from \"doj-react-adminlte/DropdownButton/DropdownButton\";\n\nexport default class ExampleDropdownButton extends React.Component{\n    render () {\n        return (\n            <DropdownButton label=\"Disabled\" className=\"btn btn-danger\" />\n        );\n    }\n}"}]},{"name":"InfoBox","description":"","props":{"colorClass":{"type":{"name":"string"},"required":false,"description":"The color of the InfoBox.","defaultValue":{"value":"\"\"","computed":false}},"iconClass":{"type":{"name":"string"},"required":true,"description":"InfoBox Icons. Check AdminLTE for the list of icons available."},"label":{"type":{"name":"string"},"required":false,"description":"Defines the label of the InfoBox.","defaultValue":{"value":"\"\"","computed":false}}},"examples":[{"name":"01-InfoBoxExample","description":"","code":"import React from 'react';\n\nimport InfoBox from \"doj-react-adminlte/InfoBox\";\n\nexport default class ExampleInfoBox extends React.Component {\n    render() {\n        return (\n            <div className=\"row\">\n                <InfoBox label=\"Camera\" colorClass=\"bg-aqua\"\n                         iconClass=\"fa fa-camera\">100 Photos</InfoBox>\n                <InfoBox label=\"Database\" colorClass=\"bg-red\"\n                         iconClass=\"fa fa-database\">935</InfoBox>\n                <InfoBox label=\"Music\" colorClass=\"bg-green\"\n                         iconClass=\"fa fa-music\">75</InfoBox>\n                <InfoBox label=\"Wifi\" colorClass=\"bg-yellow\"\n                         iconClass=\"fa fa-wifi\">Strong</InfoBox>\n            </div>\n        );\n    }\n}"}]},{"name":"Modal","description":"","props":{"className":{"type":{"name":"string"},"required":false,"description":"className gets and sets the value of the class attribute of the specified element. You can also add a CSS class in this prop to style a particular element."},"closeOnBackdropClick":{"type":{"name":"bool"},"required":false,"description":"Set to true to close the modal by just pressing the backdrop.","defaultValue":{"value":"true","computed":false}},"closeOnEscapeKey":{"type":{"name":"bool"},"required":false,"description":"Set to true to close the modal by pressing Escape key.","defaultValue":{"value":"true","computed":false}},"dialogClassName":{"type":{"name":"string"},"required":false,"description":"dialogClassName is a css class to apply to the Modal Dialog."},"fixedScroll":{"type":{"name":"bool"},"required":false,"description":"Set to true to show a scrollbar.","defaultValue":{"value":"false","computed":false}},"isLoading":{"type":{"name":"bool"},"required":false,"description":"Returns a boolean value for a postback or loading a content.","defaultValue":{"value":"false","computed":false}},"onCloseClick":{"type":{"name":"func"},"required":false,"description":"The onCloseClick prop allows passing a function that will be invoked when the close button is clicked.","defaultValue":{"value":"() => {}","computed":false}},"onEnter":{"type":{"name":"func"},"required":false,"description":"The onEnter prop allows binding a function that will be invoked when the Enter button is clicked.","defaultValue":{"value":"() => {}","computed":false}},"onExit":{"type":{"name":"func"},"required":false,"description":"The function that is binded in this event will be invoked when the user exits the Modal.","defaultValue":{"value":"() => {}","computed":false}},"show":{"type":{"name":"bool"},"required":false,"description":"Set to true to automatically show the modal.","defaultValue":{"value":"false","computed":false}},"size":{"type":{"name":"enum","value":[{"value":"'default'","computed":false},{"value":"'large'","computed":false},{"value":"'small'","computed":false}]},"required":false,"description":"You can set the size of the Modal in this prop.","defaultValue":{"value":"'default'","computed":false}}},"examples":[{"name":"01-ExampleModal","description":"","code":"import React from 'react';\nimport Modal from \"doj-react-adminlte/Modal\";\n\nexport default class ExampleModal extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            show: false\n        };\n    }\n\n    handleClick = () => {\n        this.setState({show: true});\n    };\n\n    handleCloseClick = () => {\n        this.setState({show: false});\n    };\n\n    render() {\n        return (\n            <div className='row'>\n                <div className='col-xs-4'>\n                    <button className=\"btn btn-default\" type=\"button\" onClick={this.handleClick}>\n                        Open Modal\n                    </button>\n                    <Modal className=\"modal modal-default\" show={this.state.show}\n                           onCloseClick={this.handleCloseClick} fixedScroll>\n                        <Modal.Header>\n                            Default Modal\n                        </Modal.Header>\n                        <Modal.Body>\n                            <p>\n                                Show prop is used for setting the state of the modal.\n                                Show prop is a boolean with values of either true or false.\n                            </p>\n                        </Modal.Body>\n                        <Modal.Footer>\n                            <button type=\"button\" className=\"btn btn-default pull-left\"\n                                    onClick={this.handleCloseClick}>\n                                Close\n                            </button>\n                        </Modal.Footer>\n                    </Modal>\n                </div>\n            </div>\n        );\n    }\n}"},{"name":"02-ExampleModal","description":"","code":"import React from 'react';\nimport Modal from \"doj-react-adminlte/Modal\";\n\nclass SimpleModal extends React.Component {\n    render() {\n        const {show, title, message, size, onCloseClick} = this.props;\n\n        return (\n            <Modal show={show} onCloseClick={onCloseClick} size={size}>\n                <Modal.Header>{title}</Modal.Header>\n                <Modal.Body>\n                    {message}\n                </Modal.Body>\n                <Modal.Footer>\n                    <button type=\"button\" className=\"btn btn-default pull-left\" onClick={onCloseClick}>\n                        Close\n                    </button>\n                </Modal.Footer>\n            </Modal>\n        )\n    }\n}\n\nexport default class ExampleModal extends React.Component{\n    constructor(props) {\n        super(props);\n        this.state = {\n            showSmall: false,\n            showRegular: false,\n            showLarge: false\n        };\n    }\n\n    handleSmallClick = () => {\n        this.setState({showSmall: true});\n    };\n\n    handleSmallClose = () => {\n        this.setState({showSmall: false});\n    };\n\n    handleRegularClick = () => {\n        this.setState({showRegular: true});\n    };\n\n    handleRegularClose = () => {\n        this.setState({showRegular: false});\n    };\n\n    handleLargeClick = () => {\n        this.setState({showLarge: true});\n    };\n\n    handleLargeClose = () => {\n        this.setState({showLarge: false});\n    };\n\n    render () {\n        const {showSmall, showRegular, showLarge} = this.state;\n\n        return (\n            <React.Fragment>\n                <SimpleModal show={showSmall} title=\"Small Modal\" size=\"small\"\n                             message=\"Small Modal\" onCloseClick={this.handleSmallClose}/>\n                <SimpleModal show={showRegular} title=\"Regular Modal\"\n                             message=\"Regular Modal\" onCloseClick={this.handleRegularClose}/>\n                <SimpleModal show={showLarge} title=\"Large Modal\" size=\"large\"\n                             message=\"Large Modal\" onCloseClick={this.handleLargeClose}/>\n                <div className=\"btn-group\">\n                    <button className=\"btn btn-default\" onClick={this.handleSmallClick}>Small Modal</button>\n                    <button className=\"btn btn-default\" onClick={this.handleRegularClick}>Regular Modal</button>\n                    <button className=\"btn btn-default\" onClick={this.handleLargeClick}>Large Modal</button>\n                </div>\n            </React.Fragment>\n        );\n    }\n}"},{"name":"03-ExampleModal","description":"","code":"import React from 'react';\nimport Modal from 'doj-react-adminlte/Modal';\n\nclass ThemeableModal extends React.Component {\n    render() {\n        const {className, title, show, onCloseClick} = this.props;\n\n        return (\n            <Modal className={className} show={show} onCloseClick={onCloseClick}>\n                <Modal.Header>\n                    {title}\n                </Modal.Header>\n                <Modal.Body>\n                    One fine body!\n                </Modal.Body>\n                <Modal.Footer>\n                    <button className=\"btn btn-default pull-right\" onClick={onCloseClick}>Close</button>\n                </Modal.Footer>\n            </Modal>\n        );\n    }\n}\n\nexport default class ExampleModal extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            defaultShow: false,\n            infoShow: false,\n            dangerShow: false,\n            warningShow: false,\n            successShow: false\n        };\n    }\n\n    onDefaultClick = () => {\n        this.setState({defaultShow: true});\n    };\n\n    onDefaultClose = () => {\n        this.setState({defaultShow: false});\n    };\n\n    onInfoClick = () => {\n        this.setState({infoShow: true});\n    };\n\n    onInfoClose = () => {\n        this.setState({infoShow: false});\n    };\n\n    onDangerClick = () => {\n        this.setState({dangerShow: true});\n    };\n\n    onDangerClose = () => {\n        this.setState({dangerShow: false});\n    };\n\n    onWarningClick = () => {\n        this.setState({warningShow: true});\n    };\n\n    onWarningClose = () => {\n        this.setState({warningShow: false});\n    };\n\n    onSuccessClick = () => {\n        this.setState({successShow: true});\n    };\n\n    onSuccessClose = () => {\n        this.setState({successShow: false});\n    };\n\n    render () {\n        const {defaultShow, infoShow, dangerShow, warningShow, successShow} = this.state;\n\n        return (\n            <React.Fragment>\n                <ThemeableModal show={defaultShow} onCloseClick={this.onDefaultClose}\n                                title=\"Default Modal\"/>\n                <ThemeableModal show={infoShow} className=\"modal-info\"\n                                title=\"Info Modal\" onCloseClick={this.onInfoClose}/>\n                <ThemeableModal show={dangerShow} className=\"modal-danger\"\n                                title=\"Danger Modal\" onCloseClick={this.onDangerClose}/>\n                <ThemeableModal show={warningShow} className=\"modal-warning\"\n                                title=\"Warning Modal\" onCloseClick={this.onWarningClose}/>\n                <ThemeableModal show={successShow} className=\"modal-success\"\n                                title=\"Success Modal\" onCloseClick={this.onSuccessClose}/>\n                <div className=\"btn-group\">\n                    <button onClick={this.onDefaultClick}\n                            className=\"btn btn-default\">Default</button>\n                    <button onClick={this.onInfoClick}\n                            className=\"btn btn-default\">Info</button>\n                    <button onClick={this.onDangerClick}\n                            className=\"btn btn-default\">Danger</button>\n                    <button onClick={this.onWarningClick}\n                            className=\"btn btn-default\">Warning</button>\n                    <button onClick={this.onSuccessClick}\n                            className=\"btn btn-default\">Success</button>\n                </div>\n            </React.Fragment>\n        );\n    }\n}"}]},{"name":"TabContainer","description":"","props":{"defaultTab":{"type":{"name":"string"},"required":false,"description":"You can set the default tab in this prop."},"activeTab":{"type":{"name":"string"},"required":false,"description":"You can set the active tab in this prop."},"onTabClick":{"type":{"name":"func"},"required":false,"description":"The function that is binded will be invoked after clicking the tab button."}},"examples":[{"name":"01-ExampleTabContainer","description":"","code":"import React from 'react';\n\nimport TabContainer from \"doj-react-adminlte/TabContainer\";\nimport Tab from \"doj-react-adminlte/TabContainer/Tab\";\n\nexport default class ExampleTabContainer extends React.Component {\n    render() {\n        return (\n            <TabContainer defaultTab=\"home\">\n                <Tab label=\"Home\" tabId=\"home\">\n                    <p>Tab helps you manage or group sections in your website.</p>\n                </Tab>\n                <Tab label=\"Example\" tabId=\"example\">\n                    <p>My new example tab.</p>\n                </Tab>\n            </TabContainer>\n        );\n    }\n}"},{"name":"02-ExampleTabContainer","description":"","code":"import React from 'react';\n\nimport TabContainer from \"doj-react-adminlte/TabContainer\";\nimport Tab from \"doj-react-adminlte/TabContainer/Tab\";\nimport TabToolBox from \"doj-react-adminlte/TabContainer/TabToolBox\";\n\nconst buttonStyle = {\n    width: '120px'\n};\n\nexport default class ExampleTabContainer extends React.Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            clicked: false\n        };\n    }\n\n    handleClick = () => {\n        this.setState({clicked: true});\n        setTimeout(() => {\n            this.setState({clicked: false});\n        }, 2000);\n    };\n\n    render() {\n        const {clicked} = this.state;\n\n        return (\n            <TabContainer defaultTab=\"profile\">\n                <TabToolBox>\n                    <button onClick={this.handleClick} className=\"btn btn-primary\"\n                            style={buttonStyle} disabled={clicked}>\n                        <i className=\"fa fa-thumbs-up margin-r-5\"/>\n                        {clicked ? \"Clicked!\" : \"Click Me!\"}\n                    </button>\n                </TabToolBox>\n                <Tab label=\"Home\" tabId=\"home\">\n                    <h4>Home Tab</h4>\n                </Tab>\n                <Tab label=\"Profile\" tabId=\"profile\">\n                    <h4>Profile Tab</h4>\n                </Tab>\n                <Tab label=\"Settings\" tabId=\"settings\">\n                    <h4>Settings Tab</h4>\n                </Tab>\n            </TabContainer>\n        );\n    }\n}"}]},{"name":"ValueButton","description":"","props":{"className":{"type":{"name":"string"},"required":false,"description":"className gets and sets the value of the class attribute of the specified element. You can also add a CSS class in this prop to style a particular element."},"onClick":{"type":{"name":"func"},"required":false,"description":"When the button is clicked, the corresponding function that is binded in this prop will trigger.","defaultValue":{"value":"() => {}","computed":false}},"value":{"type":{"name":"any"},"required":false,"description":"The value prop specifies the initial value for a ValueButton."}},"examples":[{"name":"01-ExampleValueButton","description":"","code":"import React from 'react';\nimport ValueButton from \"doj-react-adminlte/ValueButton\";\n\nexport default class ExampleValueButton extends React.Component {\n    constructor() {\n        super();\n        this.state = {\n            myValue: 0\n        };\n    }\n\n    handleIncrementClick = value => {\n        this.setState({ myValue: this.state.myValue + value })\n    };\n\n    render() {\n        const incrementValue = 2;\n\n        return (\n            <div>\n                <div>\n                    <span className=\"text-bold\">myValue: </span>\n                    <span>{this.state.myValue}</span>\n                </div>\n                <ValueButton className=\"btn btn-primary btn-sm\" value={incrementValue} onClick={this.handleIncrementClick}>\n                    <i className=\"fa fa-plus\" /> myValue\n                </ValueButton>\n            </div>\n        );\n    }\n}"}]}]